import React from "react";
import { Link } from "react-scroll";
import "./Home.css";
import Typewriter from "typewriter-effect";
function Home(props) {
  return (
    <>
      <div>
        <div className=" my-5">
          <span className="fw-500 text-info">Hi, my name is </span>
          <div
            style={{ fontWeight: "bold" }}
            className={`name text-${props.mode === "light" ? "dark" : "light"}`}
          >
            <h1> Md Shoyeb</h1>
          </div>
          <div
            style={{ fontWeight: "bold" }}
            className={`name text-${props.mode === "light" ? "dark" : "light"}`}
          >
            <div className="text-info">
              <Typewriter
                options={{
                  strings: [
                    "Full Stack Developer",
                    "MERN Stack Developer",
                    "software engineer",
                    "Software Developer",
                  ],
                  autoStart: true,
                  loop: true,
                }}
              />
            </div>
            <h4>Expert In Data Structure and algorithms</h4>
          </div>
          <div
            className={`message col-12 text-${
              props.mode === "light" ? "dark" : "light"
            }`}
          >
            <h1 style={{ fontWeight: "bold" }}>
              Make your business shine on the web
            </h1>
            <div className="d-flex col-md-8   justify-content-between mt-5">
              <Link
                to="contact"
                spy={true}
                smooth={true}
                offset={50}
                duration={500}
                type="button"
                className="btn border border-info  btn-outline-info"
              >
                Hire Me
              </Link>
              <a
                href="MdShoyeb_Resume.pdf"
                download="MdShoyeb_Resume.pdf"
                className="btn border border-info btn-outline-info"
              >
                Resume
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Home;
